export default {
  data() {
    return {
      userInfo: "",
      courseName: "",
      className: ""
    };
  },
  onLoad() {
    let t = this;
    t.userInfo = uni.getStorageSync("userInfo");
  },
  methods: {
    /* 创建课程 */
    toCreateClass() {
      let t = this,
        userInfo = t.userInfo;
      if (t.courseName == "") {
        t.$utils.showToast("请输入课程名");
        return;
      }
      if (t.className == "") {
        t.$utils.showToast("请输入班级名");
        return;
      }
      let data = {
        user_id: userInfo.id,
        creater: userInfo.nickName,
        courseName: t.courseName,
        className: t.className
      };
      t.$utils.ajax(t.$api.createClass, "post", data, res => {
        console.log(res);
        if (res.flag == "had") {
          t.$utils.showToast(res.msg);
          return;
        }
        t.$utils.showToast(res.msg + "即将跳转首页");
        setTimeout(() => {
          uni.switchTab({
            url: "./index"
          });
        }, 1500);
      });
    }
  }
};
